<template>
	<div class="MBox">
		<div class="playbox">
			<div class="r-title">{{audioName}}</div>
			<div class="imgbox">
				<img class='[nowclass]' :src="audioImg">
			</div>
			<div class="play-box">
				<div class="left">
					<div class="info">
						<div class="size">{{currentTime}}</div>
						<el-slider v-model="value1" value="timeVal" max="maxVal"></el-slider>
						<!-- <slider block-size="16" value="{{timeVal}}" max="{{maxVal}}" activeColor="#FF1744" block-color="#f97468" bindchange="slideBar" bindchanging="slideBar"></slider> -->
						<div class="timeshow">{{duration}}</div>
					</div>
				</div>
			</div>
			<div class="progresswrap">
          <!-- <slider style="width:500rpx;margin-left:125rpx;" block-size="16"value="{{timeVal}}"max="{{maxVal}}" activeColor="#FF1744" block-color="#f97468"show-value="true"bindchange="slideBar"></slider> -->
        </div>
			<div class="video">
				<!-- <audio poster="{{audioImg}}" name="{{audioName}}" author="{{author}}" src="{{src}}" id="myAudio" controls ></audio> -->
				<audio :src="src" id="myAudio" controls style="top: 70px;left: 0;position: fixed;"></audio>
			</div>

			<div class="ctrlbox">
				<div class="listBtn" @click="onList">
					<img src='../assets/img/list.png' @click="listOn">
				</div>
				<div class="beforeBtn" @click="PreSong">
					<img src='../assets/img/before.png'>
				</div>
				<div class="playBtn" @click="playAction">
					<img style='width:48px;height:48px;' :src="playIcon">
				</div>
				<div class="nextBtn" @click="NextSong">
					<img src='../assets/img/next.png'>
				</div>
				<div class="loopBtn">
					<img src='../assets/img/loop.png'>
				</div>
			</div>
		</div>

		<!---->
		<!-- <div class="infobox">
			<div class="detail">
				<div class="infor-head"></div>
				<text class="infor-text"></text>
			</div>
		</div> -->
		<!---->
		<!--<div class="play-list" animation="{{animationData}}" wx:if='{{playlist}}'>
			<div class='list-box'>
				<div class="Song item {{PlayDataI==index ? 'playing':''}}" wx:for="{{songslist}}" wx:key='{{index}}' id="list{{index}}" data-index="{{index}}" bindtap="playthis">
					<div class="name">{{index+1}}.{{item.name}}</div>
					 <div class="play_list__line">-</div>
            		<div class="singer">{{item.singer}}</div> 
					<img class="playing-gif" wx:if="{{PlayDataI==index}}" src="../img/playing.gif" />
				</div>
			</div>
		</div>
		<div class="mask" wx:if='{{mask}}' bindtap='offList'></div>-->
	</div>
</template>

<script>
	export default {
		mounted() {

		},
		data() {
			return {
				playlist: false,
				mask: false,
				playAction: 'audioPlay',
				currentTime: '00:00',
				duration: '00:00',
				nowclass: '',
				songslist: [{
						name: 'Apologize',
						imgsrc: 'http://p1.music.126.net/MzxX8rQNnCxKfVO_yS8_Kg==/909296116199925.jpg?param=130y130',
						songsrc: 'http://music.163.com/song/media/outer/url?id=24953439.mp3'
					},
					{
						name: '',
						imgsrc: 'http://p1.music.126.net/G5YxAyt9812z9MROfWarkg==/109951163318974870.jpg?param=130y130',
						songsrc: 'http://music.163.com/song/media/outer/url?id=566442496.mp3'
					},
					{
						name: '',
						imgsrc: 'http://p1.music.126.net/22cli1uP_NuL8TVeSVPkhg==/18762066418191386.jpg?param=130y130',
						songsrc: 'http://music.163.com/song/media/outer/url?id=22753511.mp3'
					},
					{
						name: '',
						imgsrc: 'http://p1.music.126.net/VY-LfiQZZNjtDBHGmEu85A==/109951163300149472.jpg?param=130y130',
						songsrc: 'http://music.163.com/song/media/outer/url?id=557584888.mp3'
					}
				],
				playIcon: '../image/play.png',
				audioName: '',
			}
		},
		methods: {

		}
	}
</script>

<style scoped>
.MBox{background: #f2f3f4;}
.playbox{width:100%;max-width:414px;height:100vh;margin:0 auto;background: #000000;opacity: .6;}
.r-title{text-align:center;font-size:18px;padding:20px 10px 60px;user-select:none;}
.imgbox{width:250px;height:250px;border-radius:50%;background-image:#fffcf5;margin:0 auto 50px;box-shadow:2px 2px 5px #b9b0b0;}
.imgbox img{display:block;width:100%;height:100%;border-radius:50%}
.zhuan{-webkit-transform:rotate(360deg);animation:rotation 25s linear infinite;-moz-animation:rotation 25s linear infinite;-webkit-animation:rotation 25s linear infinite;-o-animation:rotation 25s linear infinite}
@-webkit-keyframes rotation{from{-webkit-transform:rotate(0deg)}to{-webkit-transform:rotate(360deg)}}
.ctrlbox{height:auto;padding:10px 10px 20px 10px;text-align:center;display:flex;justify-content:center;align-items:center}
.listBtn{width:40px;height:40px}
.beforeBtn{width:40px;height:40px;margin-left:6%}
.playBtn{width:48px!important;height:48px;margin:1px 10%}
.nextBtn{width:40px;height:40px;background-size:40px;margin-right:6%}
.loopBtn{width:40px;height:40px}
/*.stopBtn{background-image:url(../img/stop.png);}*/
.ctrlbox div img{width:40px;height:40px}
.play-box .left{width:100%}
.progresswrap{margin-top:10px}
.play-box .left div.info{height:26px;line-height:26px;font-size:14px;color:#000;margin:0 10px;z-index:1;display:flex;align-content:center;justify-content:center}
.play-box .left div.info .size{width:10%}
.play-box .left div.info .timeshow{width:10%}
.info div{color:#777}
.info slider,.el-slider{width:70%;margin:4px auto}
.infobox{width:100%;height:300px}
.detail{height:300px;margin:10px;padding:10px;border-radius:10px;background-color:#edfdff}
.infor-head{font-size:18px;padding:10px 0;font-weight:bold;user-select:none}
.infor-text{line-height:26px;font-size:16px;color:#505050}
.play-list{width:48%;max-width:207px;height:100%;background-color:#fafbff;position:fixed;z-index:999;left:0;top:0;padding:10px 5px;overflow-y:auto;transition:width 2s;-webkit-transition:width 2s}
.play-list:focus{width:48%}
.list-box{display:flex;flex-wrap:wrap}
.play-list div .Song{width:100%;border-top:1px #bfbfbf solid;padding:8px 5px;text-overflow:ellipsis;white-space:nowrap;font-size:18px}
.playing{color:orange;display:flex;align-content:space-around;justify-content:space-between}
.playing-gif{width:10px;height:10px;float:right;position:relative;margin:auto 0}
.mask{width:100%;height:100%;background-color:rgba(0,0,0,0.53);z-index:800;position:fixed;top:0}
</style>


